import React from 'react';

 const Category = ({handleCategory}) => {
  return (
    <div className="category">
      <ul>
        <li onClick={()=> handleCategory("electronics")}>Electronics(12)</li>
        <li onClick={()=> handleCategory("faceMasks")}>face mask (3)</li>
        <li onClick={()=> handleCategory("freshFoods")}>fresh food (8)</li>
        <li onClick={()=> handleCategory("grocery")}>grocery (6)</li>
        <li onClick={()=> handleCategory("home")}>home(24)</li>
        <li onClick={()=> handleCategory("kids")}>kids (9)</li>
        <p onClick={()=> handleCategory("viewAll")}>view all categories ›</p>
      </ul>
    </div>
  )
}

export default Category;
